import { Component, OnInit } from '@angular/core';
import { ListContentsService } from '../shared/list-contents.service';

@Component({
  selector: 'app-dashboard',
  templateUrl: './dashboard.component.html',
  styleUrls: ['./dashboard.component.scss']
})
export class DashboardComponent implements OnInit {
  var1 : string = 'Welcome';
  var2 : string[];
  constructor(private getContent: ListContentsService) { }

  ngOnInit() {
    this.getContent.get().subscribe((data: any)=>{
      console.log(data);
      if (data.ls_contents)
      {
        this.var2 = data.ls_contents;
        console.log(this.var2[0]);
      }
    });
  }

}
